<div class="report-page"><h1>erfahrungsberichte</h1><div class="main"><div class="left"><div class="filter"><div class="search"><label for="search">suche:&nbsp;</label><input id="search" v-model="search"></div><ul><li class="tag" :class="{active: status === &quot;in_review&quot;}" @click="status = &quot;in_review&quot;">neu</li><li class="tag" :class="{active: status === &quot;ok&quot;}" @click="status = &quot;ok&quot;">freigeschaltet</li><li class="tag" :class="{active: status === &quot;troll&quot;}" @click="status = &quot;troll&quot;">troll</li></ul></div><hr><div class="report-row" v-for="report in reports" :key="report.id"><div class="report-preview" @click="selectedReport = report" :class="{selected: selectedReport &amp;&amp; report.id === selectedReport.id}"><div class="info"><span class="date">{{report.created_at}}&nbsp/&nbsp</span><span class="name">{{report.name}}&nbsp</span></div><div class="teaser" v-html="report.teaser||report.text"></div></div></div></div><div class="report-edit" v-if="selectedReport"><div><span class="dropdown">{{labels[selectedReport.status] || selectedReport.status}} â–¾&nbsp<div class="dropdown-content"><div class="dropdown-items"><div class="dropdown-item" :class="{active: selectedReport.status === 'in_review'}" @click="() =&gt; onStatusChange(&quot;in_review&quot;)">neu</div><div class="dropdown-item" :class="{active: selectedReport.status === 'ok'}" @click="() =&gt; onStatusChange(&quot;ok&quot;)">freigeschaltet</div><div class="dropdown-item" :class="{active: selectedReport.status === 'troll'}" @click="() =&gt; onStatusChange(&quot;troll&quot;)">troll</div></div></div></span>/ 
{{selectedReport.created_at}} / 
{{selectedReport.found_solution ? 'loesung gefunden: ' + selectedReport.found_solution : 'keine loesung gefunden'}} / 
{{selectedReport.reported ? '' : 'nicht '}}gemeldet / 
{{selectedReport.email}}</div><div>passiert wo: {{selectedReport.happened_where || '-'}} / passiert am: {{selectedReport.happened_at || '-'}}</div><hr><div><b>teaser:</b></div><div class="report-teaser" v-html="selectedReport.teaser" contenteditable @blur="onTeaserEdit"></div><hr><div><b>{{selectedReport.name}} schrieb:</b></div><div class="report-text" v-html="selectedReport.text" contenteditable @blur="onTextEdit"></div></div></div></div>